import {useEffect, lazy, Suspense, useState, useContext, useRef} from 'react';
import { Route, Switch, useRouteMatch, useHistory, Redirect, Link } from 'react-router-dom';
import { Icon } from 'react-icons-kit'
import {heartO} from 'react-icons-kit/fa/heartO';
import logo from './image/logo.svg'
import {useDispatch, useSelector} from 'react-redux';
import { post, fetchData } from '../application/middlewares/index';
import {setCategory,setFilters} from "../application/actions/data";
import { getCategory,getFilters, getUser } from '../application/selectors/auth';
import Lang1 from '../pages/Language/Lang1';
import { LangContext } from "../pages/Language/LangContext";
import {smallDown} from 'react-icons-kit/entypo/smallDown'
import {ic_check} from 'react-icons-kit/md/ic_check'

import {ic_person} from 'react-icons-kit/md/ic_person'
import {ic_notes} from 'react-icons-kit/md/ic_notes'
import {ic_keyboard_arrow_left} from 'react-icons-kit/md/ic_keyboard_arrow_left'
import {ic_logout} from 'react-icons-kit/md/ic_logout'
import {ic_add_circle_outline_outline} from 'react-icons-kit/md/ic_add_circle_outline_outline'
import useOnClickOutside from '../components/useOnClickOutside'

// import Lang1 from '../pages/Language/Lang1';
// import { LangContext } from "../pages/Language/LangContext";

function Navbar({page, setPage}){
  const { lang, dispatchLang } = useContext(LangContext);

    const dispatch = useDispatch();
    const history = useHistory();
    const categories = useSelector(getCategory);
    const user = useSelector(getUser);
    const ToggleClass1 = () => {
        setFlag(!flag);
        // setActive2(true);
        // setActive4(true);
        // setActive3(true);
       };
       const [flag, setFlag] = useState(false);
    const ref = useRef()


    useOnClickOutside(ref, () =>setFlag(false));
    // const [filter_id, setFilter_id]= useState({});
    const [logo, setLogo] = useState();
    let dil;
    if (lang=='ru') 
    {dil='ru'} 
    else 
    {dil='tm'}
    // console.log(page," -------page");
    useEffect(() => {
        dispatch(fetchData({
            url:`page-images/4`,
            token:'',
            action:(response)=>{
                if(response.success){
                    // console.log("get_location ",response)
                    // console.log(response.data.rows[0].real_estates_all[0])
                    setLogo(response.data.rows);
                    
                    // console.log(products)
                } else {
                    console.log("error_data ",response);
                    }
                }
            }))
    }, []);
    useEffect(() => {
        dispatch(fetchData({
            url:`${dil}/categories-types`,
            token:'',
            action:(response)=>{
                if(response.success){
                    // console.log("get_category ",response);
                    dispatch(setCategory({"categories":response.data.rows}));
                    // console.log("----",response.data.rows[0].main_types);
                    // setCategories(response.data.rows);
                    // console.log(categories)
                } else {
                    console.log("error_data ",response);
                    }
                }
            }))
    }, [dil]);

    
//    function Link_pushing(category_id, type_id){
//     // console.log(category_id, "     ", type_id);
//     dispatch(setFilters({"filters":{category_id, type_id}}));
//     // console.log(useSelector(getFilters));
//     history.push('/products')
//    }

    
function openNav() {
    document.getElementById("mySidenav").style.width = "100%";
    document.getElementById("mySidenav").style.left = "0";
  }
  
  function closeNav() {
    document.getElementById("mySidenav").style.width = "0";
    document.getElementById("mySidenav").style.left = "-25px";
  }
    
    return(
        <main>
            <div id="mySidenav" className="sidenav p-3 shadow-lg">
                <div className='py-2'>
            <Icon onClick={()=>closeNav()} size={35} icon={ic_keyboard_arrow_left} className="mr-1" />
                </div>
                {user?.id ?
                <Link to="/profile" className="flex font-medium text-lg py-3">
                <Icon size={25} icon={ic_person} className="mr-2" />
                    {user?.full_name}</Link>
                :
                <Link to='/auth/login' className="flex font-medium text-lg py-3 ">
            <Icon size={26} icon={ic_logout} className="mr-2" />
            <span>{Lang1[lang].login}</span>
                </Link>
                }
                
                
                <hr/>
                <Link onClick={()=>{
                    user?.id ? history.push('/create_notice') : history.push('/auth/login')
                    }} className="flex font-medium text-lg py-3 mt-1">
            <Icon size={28} icon={ic_add_circle_outline_outline} className="mr-2" />
            <span>{Lang1[lang].fff4}</span>
                </Link>
                <Link to='/wishlist' className="flex font-medium text-lg py-3 mt-1">
            <Icon size={23} icon={heartO} className="mr-2" />
            <span>{Lang1[lang].wihslist}</span>
            
                </Link>
               
                <select className="langReact1 lang font-medium py-1 pl-2 rounded-md my-3" name='lang' value={lang}
                                onChange={(e) =>{
                                    dispatchLang({ type: e.target.value });
                                    closeNav();
                                } }
                            >
                                <option className="langReact">{lang}</option>
                                <option>Ru</option>
                                <option>Tk</option>
                </select>
                <hr/>
                <Link to="/arenda/1" 
                onClick={() =>{
                    closeNav();
                } } className="flex py-3">
                    <span>{Lang1[lang].rent}</span>
                </Link>
                <Link to='/arenda/2' onClick={() =>{
                    closeNav();
                } } className="flex py-3">
                    <span>{Lang1[lang].sell}</span>
                </Link>
                <Link to='/kommercheskaya' className="flex py-3 ">
                    <span>{Lang1[lang].commerce}</span>
                </Link>



            </div>
            <div className="main">
                <div className="main1">
                    <div className="nav">
                        <div className="nav1">
                            <div className="brand flex items-center">
                                <div className='header_icon'>
                            <Icon onClick={()=>openNav()} size={26} icon={ic_notes} className=" text-black mr-1" />
                                </div>
                                <Link to="/" className="flex items-center text-lg font-bold">
                                    {
                                        logo 
                                        ?
                                        <img src={`https://gamysh.com/${logo.destination}`} className="logo_img" />
                                        :
                                        null
                                    }
                                    {/* <p className="text-blue-800 te font-bold ml-1 header_icon">gamysh</p> */}
                                </Link>
                            </div>
                            <div className="link_more">
                                {categories?.map((item)=>(
                                    <li className="nav_dropdown">
                                        {
                                            page == item.id ?
                                            <p className='border-b border-blue-600' onClick={()=>{ 
                                                // setPage(item.id);
                                                history.push(`/arenda/${item.id}`)
                                                }}  
                                             key={item.id}>{item.name}</p>
                                             :
                                             <p  onClick={()=>{ 
                                                // setPage(item.id);
                                                history.push(`/arenda/${item.id}`)
                                                }}  
                                             key={item.id}>{item.name}</p>
                                        }
                                    
                                     {/* <div style={{zIndex:100000}} className='hover_back'></div> */}
                                        <div style={{zIndex:1000000}}  className="dropdown_content border-t shadow-lg">
                                            <div className="header_text p-8">
                                                <div className="category_grid">
                                                    {item.main_types?.map((item_main)=>(
                                                    // <div className="flex">
                                                        <div className="cate_box">
                                                        <span className="font-medium text-xl mb-2" key={item_main.id}>{item_main.name}</span>
                                                        {item_main?.sub_types?.slice(0,7).map((item_sub)=>(
                                                            <Link to={`/products?type_id=${JSON.stringify([item_sub.id])}&category_id=${item.id}&main_type_id=${item_main.id}`}
                                                            //    history.push(`/products/1/3`)
                                                               key={item_sub.id} className="hover:text-blue-600 mb-3 text-base">{item_sub.name}</Link>
                                                        ))}
                                                        
                                                        </div>
                                                        // <div className="cate_box mt-9 ml-14">
                                                        // {item_main?.sub_types?.slice(6,10).map((item_sub)=>(
                                                        //     <Link onClick={()=>{ 
                                                        //        history.push(`/products/${item.id}/${item_sub.id}`)
                                                        //      }}  key={item_sub.id} className="hover:text-blue-600 mb-3 text-base">{item_sub.name}</Link>
                                                        // ))}
                                                        // </div>
                                                    //  </div>
                                                    ))}
                                                    
                                                </div>
                                                <div className="flex text-sm mt-6">
                                                {/* <p className="mr-4 hover:text-blue-600">{Lang1[lang].geo}</p> */}
                                                {/* <p className="text-gray-500 hover:text-blue-600">{Lang1[lang].MapSearching}</p> */}
                                            </div>
                                            </div>
                                        </div>
                                    </li>
                                ))}
                                <li className="nav_dropdown">
                                {/* to="" */}
                                {
                                            page == 3 ?
                                            <p className='border-b border-blue-600' onClick={()=>
                                                history.push('/kommercheskaya')
                                                }>
                                                {Lang1[lang].commerce}
                                                </p>
                                             :
                                             <p onClick={()=>
                                                history.push('/kommercheskaya')
                                                }>
                                                {Lang1[lang].commerce}
                                                </p>
                                        }
                                        
                                     {/* <div style={{zIndex:100000}} className='hover_back'></div> */}
                                    <div  style={{zIndex:1000000}} className="dropdown_content border-t shadow-lg">
                                            <div className="header_text p-8">
                                                <div className="category_grid">
                                                    {categories?.map((item1)=>(
                                                        <div className="cate_box">
                                                            <span className="font-medium text-xl mb-2" key={item1.id}>{item1.name}</span>
                                                    {item1.main_types?.map((item_main)=> {if(item_main.id ==2) return(
                                                        item_main.sub_types?.map((item_sub)=>(
                                                            <Link to={`/products?type_id=${JSON.stringify([item_sub.id])}&category_id=${item1.id}&main_type_id=2`}
                                                             key={item_sub.id} className="hover:text-blue-600 mb-3 text-base">{item_sub.name}</Link>
                                                        ))
                                                        )})}
                                                        </div>
                                                    ))}
                                                    
                                                </div>
                                                <div className="flex text-sm mt-6">
                                                {/* <p className="mr-4 hover:text-blue-600">{Lang1[lang].geo}</p> */}
                                                {/* <p className="text-gray-500 hover:text-blue-600">{Lang1[lang].MapSearching}</p> */}
                                            </div>
                                        </div>
                                    </div>
                                </li>
                            </div>
                        </div>

                        <div className="nav2">
                            <Link to="/wishlist">
                                <Icon size={17} icon={heartO} className="text-gray-700 hover:text-black mr-6 font-bold cursor-pointer"/>
                            </Link>
                            <Link onClick={()=>{
                                user?.id ? history.push('/create_notice') : history.push('/auth/login')
                            }}  className="btn1 font-medium text-sm">{Lang1[lang].fff}</Link>
                            {user?.id ?
                            // <div className="rounded-full h-10 w-10 bg-gray-500">{user.fullname}</div>
                            <Link to="/profile" className="btn2 font-medium text-sm flex items-center">
                            <Icon size={20} icon={ic_person} className="text-blue-600 mr-2" />
                                {user?.full_name}</Link>
                            :
                            <Link to="/auth/login" className="btn2 font-medium text-sm">{Lang1[lang].login}</Link>
                         }
                          <div ref={ref} onClick={ToggleClass1} className='relative ml-4 flex items-center cursor-pointer font-medium text-sm btn2 '>
                              {
                                  lang == 'ru'
                                  ?
                                  <p className='font-medium text-sm'>Русский</p>
                                  :
                                  <p className='font-medium text-sm'>Türkmen</p>
                              }
                            {flag ?  
                                    <Icon size={20} icon={smallDown} className="text-blue-500" style={{transform: "rotate(180deg)"}}/>
                                    :
                                <Icon size={20} icon={smallDown} className="text-blue-500" style={{transform: "rotate(0deg)"}} />
                                }
                            <div className={flag ? 'flex flex-col absolute top-10 rounded-md left-0 w-32 bg-blue-100' : 'hidden'}>
                                <div 
                                onClick={(e) => {
                                    e.preventDefault();
                                    dispatchLang({ type: "Ru" });
                                  }} 
                                className='w-full flex items-center px-2 py-3 hover:bg-blue-200 rounded-md'>
                                    <img src='/ru.png' className='w-6' />
                                    <p className='font-medium ml-2'>Русский</p>
                                    {
                                        lang == 'ru'
                                        ?
                                    <Icon size={18} icon={ic_check} className="text-blue-400 text-medium cursor-pointer ml-2"/>
                                        :
                                     null
                                    }
                                </div>
                                <div 
                                onClick={(e) => {
                                    e.preventDefault();
                                    dispatchLang({ type: "Tk" });
                                  }} 
                                className='w-full flex items-center px-2 py-3 hover:bg-blue-200 rounded-md'>
                                    <img src='/tm.png' className='w-6' />
                                    <p className='font-medium ml-2'>Türkmen</p>
                                    {
                                        lang == 'tk'
                                        ?
                                    <Icon size={18} icon={ic_check} className="text-blue-400 text-medium cursor-pointer ml-2"/>
                                        :
                                     null
                                    }
                                </div>
                          </div>
                          </div>
                          
                            {/* <select className="langReact1 lang cursor-pointer ml-4 text-blue-600 font-medium py-1 pl-2 rounded-md text-xs bg-blue-100 hover:bg-blue-200" name='lang' value={lang}
                                onChange={(e) => {
                                    console.log(e.target.value)
                                    dispatchLang({ type: e.target.value })
                                }}
                            >
                                <option className="langReact">{lang}</option>
                                <option>Ru</option>
                                <option>Tk</option>
                            </select> */}
                        </div>
                        <Link to="/wishlist" className="header_icon">
                                <Icon size={19} icon={heartO} className="text-gray-700 font-bold cursor-pointer"/>
                        </Link>
                    </div>
                </div>
            </div>
            <hr/>
        </main>

    )
}

export default Navbar;